<template>
  <div class = 'loading' @mousedown="popMenu2($event)" style="min-height: 100%;">
    <head-top></head-top>
    <div>
      <img @mousedown="popMenu($event ,item)" v-if="centerDialogVisible" v-for="item in items" :src = "item.imgdata" @click="edit(item)" class="mui-col-sm-6 mui-col-xs-4" style="cursor:pointer; margin-left: 45px; margin-top: 35px;white-space: normal;height: 110px;width: 22.3%; border:1px solid #E8E8E8;">
      <p v-if="centerDialogVisible1" style="margin-top:30%; margin-left: 40% ">当前文件夹暂无文件</p>
      <right-menu :pop-items="popItems" :mouse="mousePosition" @ListItemClick="list_item_click">
      </right-menu>
    </div>
  </div>
</template>
<script>
import headTop  from './Header'
import { Loading } from 'element-ui';
export default {
  components: {
    headTop
  },
  mounted(){
    var a = document.getElementsByClassName('loading')
    let loadingInstance = Loading.service({target: a[0], fullscreen: false});
    this.email = localStorage.getItem('user')
    this.$request.post('http://167.179.72.48:3000/get_mind', {
      'email': this.email
    }).then((response) => {
      if(response.data.data.length == 0){
        this.centerDialogVisible = false
        this.centerDialogVisible1 = true
      }
      else{
        console.log(response.data.data)
        this.centerDialogVisible = true
        this.items = response.data.data 
      }
      loadingInstance.close()
    })
  },
  data(){
    return {
      email: '',
      select_mindid: '',
      centerDialogVisible: false,
      centerDialogVisible1: false,
      items: [{
        imgdata: ''
      },
      {
        imgdata: ''
      }],
      mousePosition:[],
      popItems:[
        {
          txt:'收藏'
        },
        {
          txt:'删除',
        }
      ],
    }
  },
  methods:{ 
    popMenu2(e){
      if(e.button == 0 && e.srcElement.childElementCount !=0){
        this.mousePosition = ['close']
      }
    },
    popMenu(e, item){
      if(e.button == 2){
        let x = e.clientX;
        let y = e.clientY
        this.select_mindid = item.mindid
        this.mousePosition = [x, y]
      }
      else if(e.button == 0){
        this.mousePosition = ['close']
      }
    },
    edit(item){
      localStorage.setItem('mindid', item.mindid)
      localStorage.setItem('edtype', 'change')
      this.$router.push('/editor')
      location.reload()
    },
    list_item_click(it){
      switch (it){
        case 0:
          console.log(this.select_mindid)
          this.$request.post('http://167.179.72.48:3000/create_star',{
            "email": this.email,
            "creater": this.email,
            "mindid": this.select_mindid
          }).then((response) => {
            console.log(response)
            if(response.data.state == 'success'){
              this.$message({
                type: 'success',
                message: '收藏成功',
                background: 'rgba(0, 0, 0, 0.7)'
              }); 
            }
            //location.reload()
          })
          break
        case 1:
          this.$request.post('http://167.179.72.48:3000/drop_one',{
            'email': this.email,
            'mindid': this.select_mindid
          }).then((response) => {
            if(response.data.state == 'success'){
              this.$message({
                type: 'success',
                message: '删除成功',
                background: 'rgba(0, 0, 0, 0.7)'
              }); 
            }
            location.reload()
          })
          break
      }
      this.mousePosition = ['close']
    },
  }
}
</script>




