<template>
  <div>
		<div class="header_container">
      <el-row>
        <el-col :span =3>
          <img src = '../assets/Logo.png' class="avator">
        </el-col>
        <el-col :span =4  :offset = 7>
          <el-menu :default-active= "activeIndex" mode="horizontal" class="el-menu-demo"  style="opacity: 0.5;" @select="handleSelect">
            <el-menu-item index="1">首页</el-menu-item>
            <el-menu-item index="2">介绍</el-menu-item>
						<el-menu-item index="3">推荐</el-menu-item>
          </el-menu>
        </el-col>
				<el-col :span = 1 :offset =7 style="margin-top : 15px">
					<el-button type="primary" round size = "small" @click="centerDialogVisible = true">登录</el-button>
				</el-col>
				<el-col :span = 1 :offset =0 style="margin-top : 15px ;margin-left: 12px">
					<el-button type="success" round size = "small" @click="centerDialogVisible2 = true">注册</el-button>
				</el-col>
      </el-row>
  	</div>
		<el-dialog
			title="登 录"
			:visible.sync="centerDialogVisible"
			width="30%"
			center>
			<el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="60px" class="demo-dynamic">
				<el-form-item
					prop="email"
					label="邮箱"
					:rules="[
						{ required: true, message: '请输入邮箱地址', trigger: 'blur' },
						{ type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
					]"
				>
					<el-input v-model="dynamicValidateForm.email"></el-input>
				</el-form-item>
				<el-form-item
					prop="password"
					label="密码"
					:rules="[
						{ required: true, message: '请输入密码', trigger: 'blur' },
					]"
				>
					<el-input v-model="dynamicValidateForm.password" type="password"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="login_delete()">取 消</el-button>
				<el-button type="primary" @click="login()">登 录</el-button>
			</span>
		</el-dialog>

		<el-dialog
			title="注册"
			:visible.sync="centerDialogVisible2"
			size = "tiny"
			width="40%"
			center>
			<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="80px" class="demo-ruleForm">
				<el-form-item label="邮箱" prop="email">
					<el-col :span = 16 >
						<el-input v-model="ruleForm2.email"></el-input>
					</el-col>
					<el-col :span = 3 :offset = 1>
						<el-button type="success" size = "small" @click="get_pin()" 
						:loading="pinl"> 获取验证码 </el-button>
					</el-col>
				</el-form-item>
				<el-form-item label="密码" prop="pass">
					<el-col :span = 16 >
						<el-input type="password" v-model="ruleForm2.pass" autocomplete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="确认密码" prop="checkPass">
					<el-col :span = 16 >
						<el-input type="password" v-model="ruleForm2.checkPass" autocomplete="off"></el-input>
					</el-col>
				</el-form-item>	
				<el-form-item label="验证码" prop="pin">
					<el-col :span = 16 >
						<el-input  v-model="ruleForm2.pin" ></el-input>
					</el-col>
				</el-form-item>	
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="resetForm('ruleForm2')">取 消</el-button>
				<el-button type="primary" @click="submitForm('ruleForm2')">确 定</el-button>
			</span>
		</el-dialog>

    <div>
			<div id="container" class="container">
				<div id="output"></div>
			</div>
			<div id="content">
				
			</div>
			<ul class="color">
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
		</div>
  </div>
</template>
<script>
export default {
  data(){
		var validatePass = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'));
			} else {
				if (this.ruleForm2.checkPass !== '') {
					this.$refs.ruleForm2.validateField('checkPass');
				}
				callback();
			}
		};
		var validatePass2 = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请再次输入密码'));
			} else if (value !== this.ruleForm2.pass) {
				callback(new Error('两次输入密码不一致!'));
			} else {
				callback();
			}
		};
    return{
			pinl: false,
      activeIndex: '1',
			backcolor: '#49dd84',
			centerDialogVisible: false,
			centerDialogVisible2: false,
			dynamicValidateForm: {
				email: '',
				password: ''
			},
			ruleForm2: {
          pass: '',
          checkPass: '',
					email: '',
					pin: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          email: [
						{ required: true, message: '请输入邮箱地址', trigger: 'blur' },
						{ type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
					],
					pin:[
						{ required: true, message: '请输入验证码', trigger: 'blur' }
					]
        }
		}
  },
  mounted(){
			FSS("container", "output");
			var now = new Date();
			var day = now.getDay() + 1;
			this.backcolor = $(".color li:nth-child(" + day + ")").css("background-color")
			$("body").css("background-color", $(".color li:nth-child(" + day + ")").css("background-color"));
	},
	beforeRouteUpdate (to, from, next) {
		console.log(from)
		next()
  },
  methods: {
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.$request.post('http://167.179.72.48:3000/create', {
						'email': this.ruleForm2.email,
						'password': this.ruleForm2.pass,
						'pin': this.ruleForm2.pin
					}).then((response) => {
						if(response.data.state == 'success'){
							this.$message({
								message: '用户创建成功，请使用创建好的账号登录',
								type: 'success',
								duration: 3000
							});
							this.$refs[formName].resetFields();
							this.centerDialogVisible2 = false 
						}
						else{
							this.$message({
								message: response.data.info,
								type: 'warning',
								duration: 3000
							});
						}
					}).catch((error) => {
				this.$message({
						message: '网络错误,请稍后重试',
						type: 'warning',
						duration: 3000
						
					});
				})
				} else {
					this.$message({
						message: '请检查格式后重新提交',
						type: 'warning',
						duration: 3000
					});
				}
			});
		},
		get_pin(){
			this.pinl = true
			this.$request.post('http://167.179.72.48:3000/pin', {
				'email': this.ruleForm2.email
			}).then((response) => {
				console.log(response)
				if(response.data.state == 'success'){
					this.$message({
						message: '验证码发送成功',
						type: 'success',
						duration: 3000
					});
				}
				else{
					this.$message({
						message: '验证码发送失败，请稍后重试',
						type: 'warning',
						duration: 3000
					});
					
				}
				this.pinl = false
			}).catch((error) => {
				this.$message({
						message: '网络错误,请稍后重试',
						type: 'warning',
						duration: 3000
					});
				this.pinl = false
				})
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
			this.centerDialogVisible2 = false 
		},
		login_delete(){
			this.dynamicValidateForm.email = ''
			this.dynamicValidateForm.password = ''
			this.centerDialogVisible = false
		},
		login(){
			this.$request.post('http://167.179.72.48:3000/login', {
				'email' : this.dynamicValidateForm.email,
				'password': this.dynamicValidateForm.password
			}).then((response) => {
				if(response.data.state == 'success'){
					this.$message({
						message: '登录成功',
						type: 'success',
						duration:3000
					});
					localStorage.setItem('user', this.dynamicValidateForm.email)
					this.$router.push('/test')
					location.reload()
				}
				else{
					this.$message({
						message: response.data.info,
						type: 'warning',
						duration: 3000
					});
				}
			}).catch((error) => {
				this.$message({
						message: '网络错误,请稍后重试',
						type: 'warning',
						duration: 3000
					});
				})
		},
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>


<style scoped lang="less">
@import '../style/mixin';
.header_container{
		height: 60px;
		justify-content: space-between;
		align-items: center;
		padding-left: 20px;
	}
.avator{
		.wh(150px, 150px);
    margin-right: 37px;
    margin-top: -40px;
  }

	body{
		background-color: #00c1ff;
		-webkit-transition: background-color 400ms ease-in-out;
		-ms-transition: background-color 400ms ease-in-out;
		-o-transition: background-color 400ms ease-in-out;
		-moz-transition: background-color 400ms ease-in-out;
		transition: background-color 400ms ease-in-out;
	}
	.container {
		position: absolute;
		height: 100%;
		width: 100%;
	}
	#output {
		position: relative;
		opacity: 0.3;
		width: 100%;
		height: 100%;
		-webkit-transition: all 400ms ease-in-out;
		-ms-transition: all 400ms ease-in-out;
		-o-transition: all 400ms ease-in-out;
		-moz-transition: all 400ms ease-in-out;
		transition: all 400ms ease-in-out;
	}
	.color{
		width: 210px;
		height: 20px;
		margin: 0 auto;
		position: fixed;
		left: 50%;
		margin-left: -110px;
		bottom: 20px;
	}
	.color li{
		float: left;
		margin: 0 5px;
		width: 20px;
		height: 20px;
		background: #ccc;
		box-shadow: 0 0 4px #FFF;
		list-style: none;
		cursor: pointer;
	}
	.color li:nth-child(1){
		background: #00c1ff;
	}
	.color li:nth-child(2){
		background: #49dd84;
	}
	.color li:nth-child(3){
		background: #ff5a3d;
	}
	.color li:nth-child(4){
		background: #ffac3f;
	}
	.color li:nth-child(5){
		background: #2b7cd3;
	}
	.color li:nth-child(6){
		background: #ff8864;
	}
	.color li:nth-child(7){
		background: #45ada8;
	}
	.logo{
		width: 140px;
		height: 70px;
		position: fixed;
		top: 10px;
		left: 50%;
		margin-left: -70px;
		border: solid 1px #BBBBBB;
		-webkit-transition: all 400ms ease-in-out;
		-ms-transition: all 400ms ease-in-out;
		-o-transition: all 400ms ease-in-out;
		-moz-transition: all 400ms ease-in-out;
		transition: all 400ms ease-in-out;
	}
	.logo:hover{
		border: solid 1px #FFF;
		background: rgba(255, 255, 255, 0.2);
	}
</style>


