<template>
  <div>
     <div class="header_container">
      <el-row>
        <el-col :span =3>
          <img src = '../assets/Logo.png' class="avator">
        </el-col>
        <el-col :span = 2 style="opacity: 0.5;margin-top: 29px ;cursor:pointer" >
          <i class="el-icon-back" @click="reback()">返回</i>
        </el-col>
        <el-col :span = 2 style="opacity: 0.5;margin-top: 29px ; margin-left: -10px; cursor:pointer">
          <i class="el-icon-document" @click="commit()">保存</i>
        </el-col>
        <el-col :span = 2 style="margin-top: 29px ;">
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link">
              线条类型<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-minus" command="line">实线</el-dropdown-item>
              <el-dropdown-item icon="el-icon-back" command="arrow">箭头</el-dropdown-item>
              <el-dropdown-item icon="el-icon-more" command="dash">虚线</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <span class="demonstration">{{lines}}</span>
        </el-col>
      </el-row>
      <div id="jsmind_container" @mousedown="popMenu($event)"></div>
      <right-menu :pop-items="popItems" :mouse="mousePosition" @ListItemClick="list_item_click">
      </right-menu>
  	</div>
  </div>
</template>

<<script>
import jsMind from 'jsmind/js/jsmind.js'
import 'jsmind/js/jsmind.draggable.js'
import 'jsmind/js/jsmind.screenshot.js'


export default {
  name: 'HelloWorld',
  data () {
    return {
      change: false,
      req_url: '',
      email: '',
      mindid: '',
      edtype: '',
      linetype: 'line',
      lines: '直线',
      mind: { 
        "meta":{
          "name":"Mind-demo",
          "author":"hitzhaod@163.com",
          "version":"0.1"
        },
        "format":"node_tree",
        "data":{"id":"root","topic":"新主题"}
      },
      options: {
        container:'jsmind_container',
        editable:true,
        theme:'success',
      },
      popItems:[
        {
          class:'fa fa-check',
          txt:'增加子节点'
        },
        {
          txt:'删除该节点',
        }
      ],
      mousePosition:[],
      imgs: ''
    }
  },
  mounted(){
    this.email = localStorage.getItem('user')
    if(this.email == '' || this.email == null){
      this.$router.push('/')
      location.reload()
    }
    this.edtype = localStorage.getItem('edtype')
    console.log(this.edtype)
    if(this.edtype == '' || this.edtype == null){
      this.$router.push('/test')
      location.reload()
    }
    else if(this.edtype == 'change'){
      this.req_url = 'http://167.179.72.48:3000/update_mind'
      this.mindid = localStorage.getItem('mindid')
      this.$request.post('http://167.179.72.48:3000/get_value', {
        'email': this.email,
        'mindid': this.mindid
      }).then((response) => {
        var jm = new jsMind(this.options)
        jm.show(response.data.data);
      })
    }
    else if(this.edtype == "view_star"){
      this.mindid = localStorage.getItem('mindid')
      this.$request.post('http://167.179.72.48:3000/get_star_data', {
        'email': this.email,
        'mindid': this.mindid
      }).then((response) => {
        var jm = new jsMind(this.options)
        jm.show(response.data.data);
        jm.disable_edit()
      })
    }
    else{
      this.req_url = 'http://167.179.72.48:3000/new_mind'
      var jm = new jsMind(this.options)
      jm.show(this.mind);
      jm.enable_edit()
    }
  },
  methods:{
    handleCommand(command) {
      this.linetype= command
      switch (command) {
        case 'line':
          this.lines = '直线'
          break;
        case 'arrow':
          this.lines = '箭头'
          break;
        case 'dash':
          this.lines = '虚线'
          break;
      }
    },
    reback(){
      console.log(this.change)
      if(this.change == false){
        this.reset()
        this.$router.push('/test')
      }else{
        this.$confirm('还有未保存的修改, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/test')
          location.reload()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      }
    },
    reset(){
      localStorage.setItem('mindid', '')
      localStorage.setItem('edtype', '')
    },
    popMenu(e){
      var jm = jsMind.current
      let self = this;
      if(e.button ===2 && jm.get_selected_node() != null){
        let x = e.clientX;
        let y = e.clientY
        self.mousePosition = [x, y]
      }
      else if(e.button === 0 || jm.get_selected_node() == null){
        self.mousePosition = ['close']
      }
    },
    list_item_click(it){
      var jm = jsMind.current
      var selected_node = jm.get_selected_node()
      var data = {
        "line_type": this.linetype
      }
      if(this.req_url !=''){
        this.change = true
        switch (it){
        case 0:
          var nodeid = jsMind.util.uuid.newid()
          var topic = 'New Node'
          console.log(nodeid)
          var node = jm.add_node(selected_node, nodeid, topic, data)
          break
        case 1:
          var selected_id = selected_node.id
          console.log(selected_id)
          jm.remove_node(selected_id)
          break
      }
      }else{
        this.change = false
      }
      this.mousePosition = ['close']
    },
    commit(){
      if(this.req_url != ''){
        console.log(this.req_url)
        this.change = false
        var jm = jsMind.current
        jm.screenshot.shootAsDataURL()
        var c = $('canvas')
        this.imgs = c[2].toDataURL()
        this.$request.post(this.req_url, {
          'email': this.email,
          'mindid': this.mindid,
          'name' : '',
          'data' : jm.get_data('node_tree'),
          'img' : this.imgs
        }).then((response) => {
          console.log(response , 'sadas')
          if(response.data.state == 'success'){
            this.$message({
              type: 'success',
              message: '保存成功'
            }); 
          }
          this.req_url = 'http://167.179.72.48:3000/update_mind'
        })
      }
    }

  }
}
</script>


<style scoped lang="less">
@import '../style/mixin';
.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
.el-icon-arrow-down {
  font-size: 12px;
}
.header_container{
		height: 75px;
    justify-content: space-between;
    background-color: #E8E8E8;
    align-items: center;
    border-bottom:1 px solid #FFF
  }
  .avator{
		.wh(150px, 150px);
    margin-right: 37px;
    margin-top: -35px;
  }
  #jsmind_container{
    margin-top: -45px;
    float:left;
    width:100%;
    height:760px;
    border:solid 1px #ccc;
    background:#f4f4f4;
  }
  .demonstration {
    display: block;
    color: #8492a6;
    font-size: 14px;
    margin-bottom: 20px;
  }
</style>


