<template>
  <div>
      <head-top></head-top>
      <div id="jsmind_container" @mousedown="popMenu($event)"></div>
      <right-menu :pop-items="popItems" :mouse="mousePosition" @ListItemClick="list_item_click">
      </right-menu>
  </div>
</template>

<script>
import headTop from './Header'
import jsMind from 'jsmind/js/jsmind.js'
import 'jsmind/js/jsmind.draggable.js'
import 'jsmind/js/jsmind.screenshot.js'


export default {
  name: 'HelloWorld',
  data () {
    return {
      mind : {
        "meta":{
          "name":"Mind-demo",
          "author":"hitzhaod@163.com",
          "version":"0.1"
        },
        "format":"node_tree",
        "data":{"id":"root","topic":"Mind_demo","children":[
          {"id":"easy","topic":"Easy","direction":"left","children":[
              {"id":"easy1","topic":"Easy to show"},
              {"id":"easy2","topic":"Easy to edit"},
              {"id":"easy3","topic":"Easy to store"},
              {"id":"easy4","topic":"Easy to embed"},
          ]},
          {"id":"powerful","topic":"Powerful","direction":"right","children":[
              {"id":"powerful1","topic":"Base on Javascript"},
              {"id":"powerful2","topic":"Base on HTML5"},
              {"id":"powerful3","topic":"Depends on you"}
          ]},
          {"id":"other","topic":"test node","direction":"left","children":[
              {"id":"other1","topic":"I'm from local variable"},
              {"id":"other2","topic":"I can do everything"}
          ]}
        ]}
      },
      options: {
        container:'jsmind_container',
        editable:true,
        theme:'success'
      },
      popItems:[
        {
          class:'fa fa-check',
          txt:'增加子节点'
        },
        {
          txt:'删除该节点',
        }
      ],
      mousePosition:[],
      imgs: ''
    }
  },
  components: {
    headTop
  },
  mounted(){
    var jm = new jsMind(this.options)
    jm.show(this.mind);
  },
  methods:{
    popMenu(e){
      var jm = jsMind.current
      let self = this;
      if(e.button ===2 && jm.get_selected_node() != null){
        let x = e.clientX;
        let y = e.clientY
        self.mousePosition = [x, y]
      }
      else if(e.button === 0 || jm.get_selected_node() == null){
        self.mousePosition = ['close']
      }
    },
    list_item_click(it){
      var jm = jsMind.current
      var selected_node = jm.get_selected_node()
      jm.screenshot.shootAsDataURL()
      var c = $('canvas')
      console.log(c[2].toDataURL())
      this.imgs = c[2].toDataURL()
      switch (it){
        case 0:
          var nodeid = jsMind.util.uuid.newid()
          var topic = 'New Node'
          var node = jm.add_node(selected_node, nodeid, topic)
          break
        case 1:
          var selected_id = selected_node.id
          jm.remove_node(selected_id)
          break
      }
      this.mousePosition = ['close']
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#jsmind_container{
  float:left;
  width:1000px;
  height:600px;
  border:solid 1px #ccc;
  background:#f4f4f4;
}
</style>




