<template>
	<div class="manage_page fillcontain">
		<el-row style="height: 100%;">
	  		<el-col :span="4"  style="min-height: 100%; background-color: #545c64">
				<el-menu :default-active="defaultActive" style="min-height: 100%;" class="el-menu-vertical-demo" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" router>
					<el-button size = "small" type = 'primary' style = "margin-left: 38px; margin-top: 25px;margin-bottom: 25px" @click="editer()"> 新建 </el-button>
					<el-menu-item index="/test" ><i class="zhao-icon-third-icon_workfile_line"></i> &nbsp; 我的文件</el-menu-item>
					<el-menu-item index="/recent"><i class="zhao-icon-third-icon_doc_fill"></i> &nbsp; 最新编辑</el-menu-item>
					<el-menu-item index="/stars"><i class="zhao-icon-third-icon_study_fill"></i> &nbsp; 我的收藏</el-menu-item>
					<el-menu-item index="4"><i class="zhao-icon-third-icon_meeting_fill"></i> &nbsp; 协作创作</el-menu-item>
					<el-menu-item index="5"><i class="zhao-icon-third-icon_boss_fill"></i> &nbsp; 个人中心</el-menu-item>
					<el-menu-item index="/drops"><i class="zhao-icon-third-icon_trashcan"></i> &nbsp; 回收站</el-menu-item>
				</el-menu>
			</el-col>
			<el-col :span="20" style="height: 100%;overflow: auto;">
				<keep-alive>
				    <router-view></router-view>
				</keep-alive>
			</el-col>
		</el-row>
  	</div>
</template>

<script>
  export default {
		computed: {
			defaultActive: function(){
				return this.$route.path.replace('/', '');
			}
		},
	  methods:{
			editer(){
				localStorage.setItem('edtype', 'create')
				this.$router.push('/editor')
			}
		}
  }
</script>


<style lang="less" scoped>
	@import '../style/mixin';
</style>



